<template>
  <div class="verifyApply">
    <page-header @btn-action="openApp" />
    <div class="scroll-content">
      <div v-if="detail">
        <div class="title">审核申请信息</div>
        <div
          class="info"
        >【{{detail.ownerNickName}}】申请以"家庭成员"身份加入【{{detail.cityName}}】【{{detail.areaName}}-{{detail.buildName}}-{{detail.showRoomName}}】，是否同意</div>
        <div class="progress">
          <div class="item">
            <div class="top">
              <div class="left">
                <img :src="detail.ownerPic || defaultIcon" alt />
              </div>
              <div class="right flex-space">
                <div class="name">{{detail.ownerNickName}}</div>
                <div class="phone">手机尾号{{detail.ownerPhone}}</div>
              </div>
            </div>
            <div class="line">
              <div></div>
            </div>
          </div>
          <div class="item">
            <div class="top">
              <div class="left">
                <div class="symbol">?</div>
              </div>
              <div class="right">
                <div class="apply">申请加入</div>
              </div>
            </div>
            <div class="bottomLine">
              <div></div>
            </div>
          </div>
          <div class="item">
            <div class="top">
              <div class="left">
                <img :src="homeIcon" alt />
              </div>
              <div class="right">
                <div
                  class="city"
                >【{{detail.cityName}}】【{{detail.areaName}}-{{detail.buildName}}-{{detail.showRoomName}}】</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-btn">
      <div class="disagree" @click="check(2)">拒绝</div>
      <div class="agree" @click="check(1)">接受</div>
    </div>
    <van-overlay :show="showDialog" >
      <div class="dialogView">
        <div class="dialogTitle">请选择申请人身份标签</div>
        <van-cell
          class="selectCell"
          title="单元格"
          is-link
          arrow-direction="down"
          @click.stop="selectIdenti"
        />
        <div class="vanlist" v-if="showList">
          <van-cell v-for="item in list" :key="item.text" :title="item.text"/>
        </div>
        <div class="footerBtns">
          <div class="cancleBtn">返回</div>
          <div class="confirmBtn">确认</div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { getDetailService } from "../../service/verifyApplyService";
import homeIcon from "@/assets/mine/fangjian.png";
import defaultIcon from "@/assets/mine/user_Default_avata.png";
import PageHeader from "./PageHeader";
export default {
  name: "TabBar",
  components: {
    PageHeader,
  },
  data() {
    return {
      detail: null,
      homeIcon: homeIcon,
      defaultIcon: defaultIcon,
      icon: {
        xwjlogo: require("../../assets/share/shareLogo.png"),
      },
      showDialog: false,
      showList: false,
      list: [
        { text: "全部商品", value: 0 },
        { text: "新款商品", value: 1 },
        { text: "活动商品", value: 2 },
        { text: "活动商d品", value: 3 },
        { text: "活动商dd品", value: 4 },
        { text: "活动商ddd品", value: 5 },
        { text: "活动商f品", value: 6 },
        { text: "活动商ff品", value: 7 },
        { text: "活动商gg品", value: 8 },
        { text: "活动商hhh品", value: 9 },
      ],
    };
  },
  mounted() {
    let param = this.$route.params;
    this.uuid = param.uuid.split("=")[1];
    this.getDetail();

    this.androidShareOpenApp("myfamily"); //android 拉起APP
  },
  methods: {
    getDetail() {
      getDetailService({
        uuid: this.uuid, // this.getQueryVariable("uuid")
      }).then(
        (res) => {
          console.log(res);
          if (res.resultCode == 0) {
            this.detail = res.data;
          } else {
            this.$toast(res.msg);
          }
        },
        (error) => {
          // this.$toast(error);
        }
      );
    },

    /**
     * 获取url参数
     */
    getQueryVariable(variable) {
      var query = window.location.search.substring(1);
      console.log(query);
      var vars = query.split("&");
      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return false;
    },

    /**
     * 跳转到验证手机号页面
     */
    check(status) {
      if (status == 1) {
        this.showDialog = true;
        return;
      }
      if (Object.keys(this.detail).length == 0) return;
      this.$router.push({
        name: "verifyPhone",
        query: { data: JSON.stringify(this.detail), type: status },
      });
    },
    selectIdenti() {
      this.showList = !this.showList;
    },
    openApp() {
      this.shareOpenApp("myfamily");
    },
  },
};
</script>

